import React from 'react';
import PropTypes from 'prop-types';
import './PhotoCell.css';
import Skill from "../Skill/Skill";

class PhotoCell extends React.Component {
  render() {
      const imgSrc = '/images/projects/' + this.props.imageName + '.png';
      const groupsJSON = JSON.stringify(this.props.groups);

      return (
          <div
              key={this.props.id}
              className="col-lg-4 picture-item"
              data-groups={groupsJSON}
              data-title={this.props.title}
          >
              <div className="aspect aspect--16x9">
                  <div className="aspect__inner">
                      <img src={imgSrc} alt={this.props.title} />
                  </div>
                  <div className="picture-item-overlay" />
                  <div className="picture-item-hover">
                      <div className="picture-title">{this.props.title}</div>
                      <div className="picture-skills">{this.props.skills}</div>
                      <a
                          className="learn-more"
                          onClick={() => {
                              this.props.handleLearnMore(this.props.id);
                          }}
                      >
                          Learn More
                      </a>
                  </div>
              </div>
              <figcaption>{this.props.title}</figcaption>
          </div>
      );
  }
}

Skill.propTypes = {
    id: PropTypes.number,
    groups: PropTypes.array,
    imageName: PropTypes.string,
    title: PropTypes.string,
    skills: PropTypes.string,
    handleLearnMore: PropTypes.func
};

export default PhotoCell;
