import React from 'react';
import './FloatingPhoto.css';
import me from '../../assets/images/me.png';
import Singleton from '../Singleton';

class FloatingPhoto extends React.Component {
  render() {
    return (
      <div className="showAfterSecs" style={{"animationDelay": Singleton.showDelay + 's'}}>
          <div className="floating-photo">
              <a href="https://www.linkedin.com/in/yugene0530/" target="_blank" rel="noopener noreferrer">
                  <img src={me} alt="ME" />
              </a>
          </div>
      </div>
    );
  }
}

export default FloatingPhoto;
