import React from 'react';
import './PhotoGridFilter.css';
import ScrollAnimation from 'react-animate-on-scroll';

const filterOptions = [
  { id: 1, label: 'ALL', filterValue: 'all' },
  { id: 2, label: 'Flutter', filterValue: 'flutter' },
  { id: 3, label: 'React Native', filterValue: 'react' },
  { id: 4, label: 'iOS', filterValue: 'ios' },
  { id: 5, label: 'Android', filterValue: 'android' },
  { id: 6, label: 'JAVASCRIPT', filterValue: 'javascript' },
  { id: 7, label: 'PHP', filterValue: 'php' }

];

function FilterOption({ id, label, filterValue, selected, handleClick }) {
  let classString = selected ? 'filter-option selected' : 'filter-option';

  return (
    <a
      key={id}
      id={id}
      className={classString}
      onClick={() => {
        handleClick(filterValue);
      }}
    >
      {label}
    </a>
  );
}

class PhotoGridFilter extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      selected: 'all'
    };
    this.handleFilterClick = this.handleFilterClick.bind(this);
  }

  handleFilterClick(filterValue) {
    this.shuffle = window.projectShuffleContainer;
    this.shuffle.filter(filterValue);
    this.setState({
      selected: filterValue
    });
  }
  render() {
    return (
      <div className="photo-filter-container">
        {filterOptions.map(option => {
          let selected = option.filterValue === this.state.selected;
          return (
              <ScrollAnimation key={option.id} animateIn="bounceIn" style={{"animationDelay": option.id * 100 + 'ms'}}>
                <FilterOption
                    id={option.id}
                    label={option.label}
                    filterValue={option.filterValue}
                    selected={selected}
                    handleClick={this.handleFilterClick}
                />
              </ScrollAnimation>
          );
        })}
      </div>
    );
  }
}

export default PhotoGridFilter;
