import React from 'react';
import { Container, Row, Col, Form, Button } from 'react-bootstrap';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { postSendEmail } from '../../Modules/mail/mailAction';
import { Element } from 'react-scroll';
import ScrollAnimation from 'react-animate-on-scroll';
import './Contact.css';
import Singleton from "../../Components/Singleton";

class Contact extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      email: '',
      name: '',
      subject: '',
      mailbody: '',
      bShow: false
    };
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleChangeEmail = this.handleChangeEmail.bind(this);
    this.handleChangeName = this.handleChangeName.bind(this);
    this.handleChangeSubject = this.handleChangeSubject.bind(this);
    this.handleChangeMailBody = this.handleChangeMailBody.bind(this);
    this.showComponent = this.showComponent.bind(this);
  }

  showComponent() {
    this.setState({
      bShow: true
    });
  }

  componentDidMount() {
    setTimeout(this.showComponent, Singleton.showDelay * 1000);
  }

  handleChangeEmail(event) {
    this.setState({
      email: event.target.value
    });
  }
  handleChangeName(event) {
    this.setState({
      name: event.target.value
    });
  }
  handleChangeSubject(event) {
    this.setState({
      subject: event.target.value
    });
  }
  handleChangeMailBody(event) {
    this.setState({
      mailbody: event.target.value
    });
  }

  handleSubmit(event) {
    //this.props.mailActions.postSendEmail()
    event.preventDefault();

    if (this.props.loading === true) return;

    const data = {
      name: this.state.name,
      email: this.state.email,
      subject: this.state.subject,
      mailbody: this.state.mailbody
    };
    this.props.mailActions.postSendEmail(data);
  }

  render() {
    let messageText = 'Your message was sent, thank you!';
    let messageClass = 'message';
    let messageIcon = 'fa fa-check';
    if (this.props.loading === true) {
      messageClass = 'message hide';
    }
    if (this.props.success === false && this.props.failed === false) {
      messageClass = 'message hide';
    }

    if (this.props.success === false && this.props.failed === true) {
      messageClass = 'message failed';
      messageText = 'Your message is not delivered. Please try again. Thanks!';
      messageIcon = 'fa fa-exclamation-triangle';
    }
    let hideOrShow = this.state.bShow === true ? '' : ' hidden';

    return (
      <Element
        id="contact-section"
        className={'contact-section' + hideOrShow}
      >
        <Container>
          <ScrollAnimation animateIn="bounceInLeft">
            <div className="title bounceInLeft">Contact</div>
          </ScrollAnimation>
          <ScrollAnimation animateIn="bounceInRight">
            <div className="title-bar" />
          </ScrollAnimation>
          <Row>
            <Col md={{ span: 6, offset: 3 }} xs="12">
              <Form onSubmit={this.handleSubmit}>
                <ScrollAnimation animateIn="fadeIn">
                  <Form.Control
                      required
                      type="text"
                      name="name"
                      placeholder="Name"
                      onChange={this.handleChangeName}
                  />
                  <Form.Control
                      required
                      type="email"
                      name="email"
                      placeholder="Email"
                      onChange={this.handleChangeEmail}
                  />
                  <Form.Control
                      required
                      type="text"
                      name="subject"
                      placeholder="Subject"
                      onChange={this.handleChangeSubject}
                  />
                  <Form.Control
                      required
                      className="subject"
                      rows="5"
                      as="textarea"
                      name="mailbody"
                      placeholder="Hello, Eugene"
                      onChange={this.handleChangeMailBody}
                  />
                  <div className={messageClass}>
                    <span className={messageIcon} />
                    {messageText}
                  </div>
                </ScrollAnimation>
                <ScrollAnimation animateIn="bounceInRight">
                  <Button
                      type="submit"
                      className="submit-button"
                      value="Submit"
                  >
                    Submit
                  </Button>
                </ScrollAnimation>
              </Form>
            </Col>
          </Row>
        </Container>
      </Element>
    );
  }
}

export default connect(
  state => ({
    loading: state.mail.loading,
    error: state.mail.error,
    success: state.mail.success,
    failed: state.mail.failed
  }),
  dispatch => ({
    mailActions: bindActionCreators(
      {
        postSendEmail
      },
      dispatch
    )
  })
)(Contact);
